
/* Drawer */
.contentWrapperHeaderSearchpage {
    display: flex;
    align-items: center;
    /*  */
    /* width: 100%; */
    justify-content: space-between;
    background-image: url("https://www.transparenttextures.com/patterns/brushed-alum.png"),
     linear-gradient(45deg, rgba(63,66,159,1) 0%, rgba(3,195,255,1) 50%, rgba(255,221,0,1) 100%);
    border-radius: 14px;
    padding: 20px 40px;
    transition: all 0.3s ease-in-out;
    /* transform: translateX(0) translateY(-20vh); */
    /* z-index: -20; */
    position: fixed;
    z-index: -20;
    width: 67vw;
    height: 0vh;
    opacity: 0;
    overflow: scroll;
    padding-top: 5vh;
  }

.contentWrapperHeaderSearchpage__isopen {
  position: fixed;
  transition: all 0.5s ease-in-out;
  /* transform: translateX(0) translateY(0vh); */
  z-index: 1;  
  width: 67vw; 
  height: 45vh;
  opacity: 1;
  overflow: scroll;
}

.contentWrapperHeaderSearchpage__isopen::-webkit-scrollbar {
  display: none;
} /* for Chrome in window os */

.contentWrapperContext {
  width: 100%;
}

.DrawerContainer {
  height: auto ;
  display: flex;
  flex-direction: column; 
  overflow-x: scroll;
}
.DrawerContainer::-webkit-scrollbar {
  display: none;
} /* for Chrome in window os */

.DrawerContainer_SubtextRow {
  /* font-size: 18px; */
  border-radius: 10px;
  display: flex;
  flex-direction: row;  
  align-items: center;
  justify-content: space-around;
}

.DrawerContainer_SubtextRow p {
  width: 30%;
}

/* Buttons */
.dropdownParent{
  position: fixed;
  left: 95%;
  top: 1.5%;
  transform: rotate(0turn);
  transition: all 0.5s ease;
}

.dropdownParent:hover {
  cursor: pointer;
  transform: rotate(0.3turn);
  transition: all 0.5s ease;
}

.dropdown {
  /* display: none !important; */
  /* display: block; */
  /* be overwritten ......  */
  position: fixed;
  left: 89%;
  top: 10%;
  z-index: 100;
  display: flex;
  flex-direction: column;
  transform-origin: right top;
  transform: scale(0) ;
  transition: all 0.3s ease;
}

.dropdown__isactive {
  /* display: inherit; */
  background-color: rgba(61, 74, 110, 1);
  border-radius: 14px;
  border: 1px solid rgba(255, 255, 255, 0.582);
  padding: 1vh;
  
  position: fixed;
  left: 89%;
  top: 10%;
  z-index: 100;
  display: flex;
  flex-direction: column;
  transform: scale(1) ;
  transition: all 0.3s ease;
  opacity: 1;
  pointer-events: all;
}

.dropdown__isactive button:hover {
  transform: translateY(-0.5vh);
}

/* goTop button */

.goTop {
  position: fixed;
  left: 93%;
  bottom: 5%;
}

.goTop:hover {
  cursor: pointer;
}

















